
import throttle from 'lodash-es/throttle';

export class MessageFeedSocket {
    private socket: WebSocket;
    private buffer: any[] = [];
    private timeout: any = null;

    private url: string = null;
    private reconnectOnClose: boolean = false;

    private rateLimits: any = {};
    private _retry = null;

    constructor( url: string ) {

    }

    public connect( url: string ) {
        try {
            if ( this.socket && this.socket.readyState === this.socket.OPEN ) {
                console.warn( 'Connection already present' );
                return;
            }
            this.url = url;
            this.reconnectOnClose = true;
            const retry = function() {
                if ( this._retry ) {
                    clearTimeout( this._retry );
                }
                this._retry = setTimeout( () => { this.connect( url ); }, 3000 );
            }.bind( this );

            /* Check for reconnections, where we already have a socket
               but it is currently being closed, or so on .. */

            if ( this.socket && this.socket.readyState !== this.socket.CLOSED ) {
                console.warn( 'Socket is currently closing, or otherwise occupied.. waiting a while..', this._retry );
                retry();
                return;
            }

            /* If we have reached here, we either have a closed socket or
               we haven't got one at all ... recreate */
            this.socket = new WebSocket( 'ws://' + url );
            if ( !this.socket ) {
                throw ( 'Could not connect' );
            }
            this.socket.addEventListener( 'message', this.message.bind( this ) );
            this.socket.addEventListener( 'error', ( err ) => {
                this.send( { '_error': 'Connection failed' } );
                this.error();
                retry();
            } );
            this.socket.addEventListener( 'open', () => {
                this.send( { '_status': 'connected' } );
                /* Clear the buffered messages, if there are any */
                this.sendToUnit();
            } );
            this.socket.addEventListener( 'close', () => {
                this.send( { '_status': 'disconnected' } );
                this.closed();
                retry();
            } );

        } catch ( e ) {
            console.error( 'Connection failed', e.message );
        }
    }

    /* Error handling function ... */
    public error( err = null ) {
        console.log( 'Websocket connection error ', err );
    }

    /* Function called when the socket is closed ... */
    public closed() {
        if ( this.reconnectOnClose ) {
            this.connect( this.url );
        }
    }

    public disconnect() {
        this.reconnectOnClose = false;
        this.socket.close();
    }

    public limit( object_type: string, wait: number ) {
        /* Take the object type and limit rate and assign them accordingly */
        let counter = 0;

        const limiter = throttle( ( data ) => {
            data._count = counter;
            this.send( data );
            counter = 0;
        }, wait );

        this.rateLimits[ object_type.toLowerCase() ] = ( data ) => {
            counter++;
            return limiter( data );
        };
    }

    public message( e: MessageEvent ) {
        const data = JSON.parse( e.data );
        try {
            /* FIXME Analyse the message and determine what to do with it, if we discriminate here then we
               can reduce the workload of the main thread */

            /* Check if there is a rate limit on messages of this type, and use it
               if there is */

            const sender = this.rateLimits[ data.object_type.toLowerCase() ] ||
                        ( ( data ) => {
                            data._count = 1;
                            this.send( data );
                        } );

            switch ( data.object_type ) {
                case 'Heartbeat':
                    sender( data );
                    break;
                default:
                    sender( data );
                    break;
            }
        } catch ( e ) {
            console.warn( 'Unable to send', e.message );
        }
    }

    public send( data: any ) {
        let transfer: ArrayBuffer = null;
        if ( data && typeof( data ) === 'object' && data.object_type === 'FibreShot' ) {
            data.amplitudes = ( new Float32Array( data.amplitudes ) );
            transfer = data.amplitudes.buffer;
        }
        if ( transfer ) {
            ( <any>postMessage )( data, [ transfer ] );
        } else {
            ( <any>postMessage )( data );
        }
    }

    public sendToUnit( data: any = null ) {
        if ( this.socket && this.socket.readyState !== WebSocket.OPEN ) {
            /* If the socket isn't connected yet, buffer the thing
               we wanted to say, give up and try again ... */

            clearTimeout( this.timeout );
            if ( data ) {
                this.buffer.push( data );
            }

            if ( this.socket.readyState === WebSocket.CLOSED || this.socket.readyState === WebSocket.CLOSING ) {
                /* If the socket is closed, there's not much point waiting
                   for it to be open again ... */
                return;
            }

            this.timeout = setTimeout( this.sendToUnit.bind( this ), 1000 );
            return;
        }

        /* If we have a buffer, we should clear it */
        this.buffer.forEach( ( message ) => {
            this.socket.send( message );
        } );
        this.buffer.length = 0;

        /* Being called to clear the buffer will likely leave us with no
           data to send, so in that case we shouldn't try to send it */
        if ( this.socket && data ) {
            this.socket.send( data );
        }
    }
}


/* And create a global instance of ourselves too */
const hs = new MessageFeedSocket( 'ws://localhost:57006' );

onmessage = ( e: MessageEvent ) => {
    /* We received a message from the UI */
    if ( hs ) {
        const data = e.data;
        if ( data.command ) {
            switch ( data.command ) {
                case 'connect':
                    hs.connect( data.url );
                    break;
                case 'disconnect':
                    hs.disconnect();
                    break;
                case 'limit':
                    hs.limit( data.type, data.wait );
                    break;
                default:
                    break;
            }
        } else {
            hs.sendToUnit( JSON.stringify( data ) );
        }
    }
};
